* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  --main-color: #00acdc;
  font-family: Roboto, serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav-links {
  align-items: center;
  font-size: 12px;
  line-height: 60px;
  list-style: none;
  display: flex;
}

.nav-links > li:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav-link:link, .nav-link:visited {
  color: #000;
}

.nav-link:hover, .nav-link:active {
  color: var(--main-color);
}

.nav-link.is-active {
  color: var(--main-color);
  position: relative;
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px 0 0;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
